import React, { Component, Fragment } from 'react';
import { Helmet } from "react-helmet";

import MainHeader from '../components/MainHeader';
import { LottieProgrammer } from '../components/LottieControl';

class About extends Component {
  constructor(props) {
    super(props);
    this.state = {};
  }

  render() {
    const {
      app,
      history,
    } = this.props;

    const title = "";
    const description = "";

    return (
      <Fragment>
        <Helmet>
          <title>{`${title} - SPASSR`}</title>
          <meta name="description" content={description} />
          <meta property="og:title" content={`${title} - SPASSR`} />
          <meta property="og:description" content={description} />
          <meta property="og:site_name" content="SPASSR" />
          <meta property="og:url" content={`${app.MAIN_URL}about`} />
          <meta itemProp="name" content={`${title} - SPASSR`} />
          <meta itemProp="description" content={description} />
          <link rel="canonical" href={`${app.MAIN_URL}about`} />
        </Helmet>
        <MainHeader history={history} />
        <div className="container">
          <LottieProgrammer />
        </div>
      </Fragment>
    );
  }
}

export default About;
